<template>
    <div class="list row">
        <div class="col-md-8">
            <div class="input-group mb-3">
                <input type="text"
                    class="form-control"
                    placeholder="Search by Title"
                    v-model="title"
                />
                <div class="input-group-append">
                <button class="btn btn-outline-secondary" type="button" @click.prevent="searchTitle"></button>
            </div>
            </div>            
        </div>
        <div class="col-md-6">
            <h4>Tutorialss List</h4>
            <ul class="list-group">
                <li
                    class="list-group-item"
                    :class="{active : index == currentI}"
                    v-for="(tutorial,index) in tutorials"
                    :key="index"
                    @click="setActiveTutorial(tutorial, index)"
                >
                {{ tutorial.title }}
            </li>
            </ul>
            <button class="m-3 btn btn-sm btn-danger" @click.prevent="removeAllTutorials">
                RemoveAll
            </button>
        </div>
        <div class="col-md-6">
            <div v-if="currentT">
                <div>
          <label><strong>Title:</strong></label> {{ currentT.title }}
        </div>
        <div>
          <label><strong>Description:</strong></label> {{ currentT.description }}
        </div>
        <div>
          <label><strong>Status:</strong></label> {{ currentT.published ? "Published" : "Pending" }}
        </div>
        <a  class="badge badge-warning" :href="'/tutorials/' + currentT.id" style="color: red;">Edit</a> 
        <!-- 이건 router의 tutorials로 요청 -->
            
        </div>
        <div v-else>
            <br>
            <p>please click any tutorial</p>
        </div>
    </div>
</div>

</template>

<script>
import TutorialDataService from '@/services/TutorialDataService';

export default{
    name: "tutorials-list",
    data(){
        return{
            tutorials : [],
            currentT : null,
            currentI : -1,
            title : ""
        }
    },
    methods : {
        retriveTutorials(){
            TutorialDataService.getAll()
                .then(res=>{
                    this.tutorials = res.data;
                    console.log(res.data);
                })
        },
        searchTitle(){
            TutorialDataService.findByTitle(this.title)
                .then(res=>{
                    this.tutorials =res.data;
                    console.log(res.data)
                })
                .catch(e=>{
                    console.log(e)
                })

        },
        setActiveTutorial(tutorial, index){
            this.currentI = index;
            this.currentT = tutorial;
        },
        refreshTutorial(){
            this.retriveTutorials();
            this.currentT = null;
            this.currentI = -1;
        }
        ,
        removeAllTutorials(){
            TutorialDataService.deleteAll()
                .then(res=>{
                    console.log(res.data)
                })
                .catch(e=>{
                    console.log(e)
                })
        }

    },
    mounted(){
        this.retriveTutorials();
    }
}

</script>
<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>