import http from "../http-common.js";

//클래스 선언은 http 객체를 사용하여, 객체 지향 프로그래밍을 지원하기 위한 개념. 이를 이용해 다양한 모듈, 재사용가능한 구성요소 구성
class TutorialDataService{
    getAll(){
        return http.get("/tutorials");
    }

    get(id){
        return http.get(`/tutorials/${id}`);
    }

    create(data){
        return http.post("/tutorials", data);
    }

    update(id, data) {
        return http.put(`/tutorials/${id}`, data);
      }
    
      delete(id) {
        return http.delete(`/tutorials/${id}`);
      }
      deleteAll(){
        return http.delete(`/tutorials`);
      }

      findByTitle(title) {
        return http.get(`/tutorials?title=${title}`);
      }
}

export default new TutorialDataService();

// HTTP 메서드에는 GET, POST, PUT, DELETE 외에도 다양한 메서드가 있습니다. 그 중 일부는 다음과 같습니다:

// HEAD: 서버에서는 GET 요청과 동일한 응답을 반환하지만, 실제 컨텐츠는 포함되지 않습니다. 헤더 정보만 반환됩니다. 주로 리소스의 메타데이터를 확인할 때 사용됩니다.
// PATCH: 리소스의 일부만 업데이트할 때 사용됩니다. PUT과 달리 리소스의 전체를 업데이트하는 것이 아니라 일부만 업데이트합니다.
// OPTIONS: 서버에서 지원하는 메서드들을 확인하기 위해 사용됩니다. 클라이언트는 서버에게 어떤 메서드들을 사용할 수 있는지 묻습니다.
// TRACE: 클라이언트 요청이 서버에 도달했을 때, 해당 요청이 서버를 통과하는 동안에 어떤 변화가 있는지를 확인하기 위해 사용됩니다.
// POST와 PUT의 차이점은 다음과 같습니다:

// POST: 주로 리소스를 생성할 때 사용됩니다. 새로운 리소스를 생성하거나, 기존 리소스에 추가 데이터를 전송할 때 주로 사용됩니다. 요청이 반복되어도 동일한 리소스가 생성됩니다. 일반적으로 서버에서 리소스 ID를 생성하고 반환합니다.

// PUT: 주로 리소스를 업데이트할 때 사용됩니다. 요청 본문에 업데이트할 리소스의 전체 데이터를 포함시킵니다. 즉, 클라이언트가 리소스의 전체를 업데이트하는 데 사용됩니다. 만약 해당 리소스가 존재하지 않으면 새로 생성될 수 있습니다. 클라이언트가 리소스의 ID를 이미 알고 있다고 가정할 때 사용됩니다.